// SummarizePrayerScheduleAndSendWhatsapp flow
'use server';
/**
 * @fileOverview A flow to summarize the prayer schedule and send it to a WhatsApp number.
 *
 * - summarizePrayerScheduleAndSendWhatsapp - A function that handles the summarization and sending process.
 * - SummarizePrayerScheduleAndSendWhatsappInput - The input type for the summarizePrayerScheduleAndSendWhatsapp function.
 * - SummarizePrayerScheduleAndSendWhatsappOutput - The return type for the summarizePrayerScheduleAndSendWhatsapp function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const SummarizePrayerScheduleAndSendWhatsappInputSchema = z.object({
  prayerSchedule: z.string().describe('A table containing the prayer schedule with names and times.'),
  whatsappNumber: z.string().describe('The WhatsApp number to send the summary to.'),
});
export type SummarizePrayerScheduleAndSendWhatsappInput = z.infer<typeof SummarizePrayerScheduleAndSendWhatsappInputSchema>;

const SummarizePrayerScheduleAndSendWhatsappOutputSchema = z.object({
  success: z.boolean().describe('Whether the summary was successfully sent to the WhatsApp number.'),
  message: z.string().describe('A message indicating the status of the operation.'),
});
export type SummarizePrayerScheduleAndSendWhatsappOutput = z.infer<typeof SummarizePrayerScheduleAndSendWhatsappOutputSchema>;

export async function summarizePrayerScheduleAndSendWhatsapp(input: SummarizePrayerScheduleAndSendWhatsappInput): Promise<SummarizePrayerScheduleAndSendWhatsappOutput> {
  return summarizePrayerScheduleAndSendWhatsappFlow(input);
}

const summarizePrayerSchedulePrompt = ai.definePrompt({
  name: 'summarizePrayerSchedulePrompt',
  input: {schema: SummarizePrayerScheduleAndSendWhatsappInputSchema},
  output: {schema: z.string()},
  prompt: `You are an assistant that summarizes a prayer schedule and prepares it for sending to a WhatsApp number.

  Summarize the following prayer schedule in a concise and informative message:
  {{prayerSchedule}}
  Make sure to thank everyone for their participation.`,
});

const sendWhatsappMessage = ai.defineTool({
  name: 'sendWhatsappMessage',
  description: 'Sends a message to a WhatsApp number.',
  inputSchema: z.object({
    phoneNumber: z.string().describe('The phone number to send the message to.'),
    message: z.string().describe('The message to send.'),
  }),
  outputSchema: z.boolean(),
},
async (input) => {
  // TODO: Replace with a real WhatsApp API provider if needed. This is a mock implementation.
  console.log(`Sending message to WhatsApp number ${input.phoneNumber}: ${input.message}`);
  // Simulate network delay
  await new Promise(resolve => setTimeout(resolve, 1000));
  // Simulate a successful API call
  return true;
});


const summarizePrayerScheduleAndSendWhatsappFlow = ai.defineFlow(
  {
    name: 'summarizePrayerScheduleAndSendWhatsappFlow',
    inputSchema: SummarizePrayerScheduleAndSendWhatsappInputSchema,
    outputSchema: SummarizePrayerScheduleAndSendWhatsappOutputSchema,
  },
  async input => {
    const {output: summary} = await summarizePrayerSchedulePrompt(input);

    if (!summary) {
        return {
          success: false,
          message: 'Falha ao gerar o resumo da escala de oração.',
        };
    }

    try {
      const success = await sendWhatsappMessage({
        phoneNumber: input.whatsappNumber,
        message: summary,
      });

      if (success) {
        return {
          success: true,
          message: 'Resumo da escala de oração enviado com sucesso para o WhatsApp.',
        };
      } else {
        return {
          success: false,
          message: 'Falha ao enviar o resumo da escala de oração para o WhatsApp.',
        };
      }
    } catch (error: any) {
      console.error('Error sending WhatsApp message:', error);
      return {
        success: false,
        message: `Falha ao enviar o resumo da escala de oração para o WhatsApp: ${error.message}`,
      };
    }
  }
);
