'use client';

import { zodResolver } from '@hookform/resolvers/zod';
import { addDays, format } from 'date-fns';
import { ptBR } from 'date-fns/locale';
import { Calendar as CalendarIcon, Clock, HelpingHand, Send, User, CheckCircle2, Loader2 } from 'lucide-react';
import React, { useState, useMemo, useEffect, useCallback } from 'react';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { summarizePrayerScheduleAndSendWhatsapp } from '@/ai/flows/summarize-prayer-schedule-and-send-whatsapp';
import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { useToast } from '@/hooks/use-toast';
import { cn } from '@/lib/utils';
import { WhatsappIcon } from './icons';

type Slot = {
  time: string;
  isBooked: boolean;
  bookedBy: string | null;
};

const bookingFormSchema = z.object({
  name: z.string().min(2, { message: 'O nome deve ter pelo menos 2 caracteres.' }).max(50),
});

const generateTimeSlots = (): Slot[] => {
  const slots: Slot[] = [];
  // Reduced to 3 slots for easier testing.
  for (let i = 6; i < 9; i++) {
    const startTime = `${String(i).padStart(2, '0')}:00`;
    const endTime = `${String(i + 1).padStart(2, '0')}:00`;
    slots.push({
      time: `${startTime} - ${endTime}`,
      isBooked: false,
      bookedBy: null,
    });
  }
  return slots;
};

export function PrayerSchedule() {
  const [date, setDate] = useState<Date | undefined>(new Date());
  const [slots, setSlots] = useState<Slot[]>(generateTimeSlots());
  const [selectedSlot, setSelectedSlot] = useState<Slot | null>(null);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [isSending, setIsSending] = useState(false);

  const { toast } = useToast();

  const form = useForm<z.infer<typeof bookingFormSchema>>({
    resolver: zodResolver(bookingFormSchema),
    defaultValues: { name: '' },
  });

  const bookedSlots = useMemo(() => slots.filter((s) => s.isBooked), [slots]);
  const allSlotsBooked = useMemo(() => slots.every((s) => s.isBooked), [slots]);

  const handleSendToWhatsApp = useCallback(async (currentSlots: Slot[]) => {
    setIsSending(true);

    const currentBookedSlots = currentSlots.filter((s) => s.isBooked);
    const scheduleTableString = `Escala de Oração - ${format(date!, 'PPP', { locale: ptBR })}\n\n| Horário | Nome |\n|---|---|\n${currentBookedSlots
      .map((s) => `| ${s.time} | ${s.bookedBy} |`)
      .join('\n')}`;

    try {
      const result = await summarizePrayerScheduleAndSendWhatsapp({
        prayerSchedule: scheduleTableString,
        whatsappNumber: '77991395904',
      });

      if (result.success) {
        toast({
          title: 'Sucesso!',
          description: result.message,
          variant: 'default',
        });
      } else {
        throw new Error(result.message);
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Ocorreu um erro desconhecido.';
      toast({
        title: 'Erro ao Enviar',
        description: errorMessage,
        variant: 'destructive',
      });
    } finally {
      setIsSending(false);
    }
  }, [date, toast]);
  
  useEffect(() => {
    // Reset schedule when date changes
    setSlots(generateTimeSlots());
  }, [date]);

  const handleSelectSlot = (slot: Slot) => {
    if (!slot.isBooked) {
      setSelectedSlot(slot);
      setIsDialogOpen(true);
      form.reset();
    }
  };

  const handleBookingSubmit = (values: z.infer<typeof bookingFormSchema>) => {
    if (selectedSlot) {
      const updatedSlots = slots.map((s) =>
        s.time === selectedSlot.time ? { ...s, isBooked: true, bookedBy: values.name } : s
      );
      setSlots(updatedSlots);
      setIsDialogOpen(false);
      setSelectedSlot(null);
      toast({
        title: 'Horário Agendado!',
        description: `Obrigado, ${values.name}. Sua hora de oração foi confirmada.`,
        className: 'bg-primary text-primary-foreground',
      });
      
      const areAllSlotsBooked = updatedSlots.every((s) => s.isBooked);
      if (areAllSlotsBooked) {
        handleSendToWhatsApp(updatedSlots);
      }
    }
  };

  return (
    <div className="space-y-8">
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <CalendarIcon className="w-6 h-6" />
            1. Escolha o Dia da Oração
          </CardTitle>
          <CardDescription>Selecione a data para ver e escolher os horários disponíveis.</CardDescription>
        </CardHeader>
        <CardContent>
          <Popover>
            <PopoverTrigger asChild>
              <Button
                variant={'outline'}
                className={cn(
                  'w-[280px] justify-start text-left font-normal',
                  !date && 'text-muted-foreground'
                )}
              >
                <CalendarIcon className="mr-2 h-4 w-4" />
                {date ? format(date, 'PPP', { locale: ptBR }) : <span>Escolha uma data</span>}
              </Button>
            </PopoverTrigger>
            <PopoverContent className="w-auto p-0">
              <Calendar
                mode="single"
                selected={date}
                onSelect={setDate}
                initialFocus
                locale={ptBR}
                disabled={(day) => day < addDays(new Date(), -1)}
              />
            </PopoverContent>
          </Popover>
        </CardContent>
      </Card>

      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Clock className="w-6 h-6" />
            2. Selecione um Horário
          </CardTitle>
          <CardDescription>
            Clique em um horário vago para agendar sua hora de oração. Os horários disponíveis são 6h, 7h e 8h.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4">
            {slots.map((slot) => (
              <Button
                key={slot.time}
                variant={slot.isBooked ? 'secondary' : 'default'}
                className="h-20 flex flex-col items-start p-3 transition-all duration-300 ease-in-out transform hover:scale-105 shadow-md"
                onClick={() => handleSelectSlot(slot)}
                disabled={slot.isBooked}
                style={{
                  backgroundColor: slot.isBooked ? '' : 'hsl(var(--accent))',
                  color: slot.isBooked ? '' : 'hsl(var(--accent-foreground))',
                }}
              >
                <div className="font-bold text-lg">{slot.time}</div>
                <div className="flex items-center gap-1 text-sm mt-1">
                  {slot.isBooked ? (
                    <>
                      <User className="w-4 h-4" />
                      <span>{slot.bookedBy}</span>
                    </>
                  ) : (
                    <>
                      <HelpingHand className="w-4 h-4" />
                      <span>Disponível</span>
                    </>
                  )}
                </div>
              </Button>
            ))}
          </div>
        </CardContent>
      </Card>

      {bookedSlots.length > 0 && (
        <Card className="shadow-lg">
          <CardHeader>
            <CardTitle>Escala de Oração do Dia</CardTitle>
            <CardDescription>
              Abaixo está a lista de irmãos comprometidos com a oração para o dia selecionado.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead className="w-[150px]">Horário</TableHead>
                  <TableHead>Nome do Membro</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {bookedSlots.map((slot) => (
                  <TableRow key={slot.time}>
                    <TableCell className="font-medium">{slot.time}</TableCell>
                    <TableCell>{slot.bookedBy}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      )}

      {allSlotsBooked && (
        <Card className="bg-primary text-primary-foreground shadow-lg animate-fade-in">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <CheckCircle2 className="w-6 h-6" />
              Torre Completa!
            </CardTitle>
            <CardDescription className="text-primary-foreground/80">
              Todos os horários foram preenchidos. Agradecemos a todos os participantes!
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
             {isSending ? (
                <div className="flex items-center justify-center gap-2">
                  <Loader2 className="h-5 w-5 animate-spin" />
                  <span>Enviando escala para o WhatsApp...</span>
                </div>
              ) : (
                <p>A escala de oração foi enviada para o número de WhatsApp configurado.</p>
              )}
          </CardContent>
        </Card>
      )}

      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>Agendar Horário de Oração</DialogTitle>
            <DialogDescription>
              Você está agendando o horário das {selectedSlot?.time}. Por favor, insira seu nome para confirmar.
            </DialogDescription>
          </DialogHeader>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(handleBookingSubmit)} className="space-y-8 p-4">
              <FormField
                control={form.control}
                name="name"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Seu Nome</FormLabel>
                    <FormControl>
                      <Input placeholder="Digite seu nome completo" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <DialogFooter>
                <Button type="submit" className="bg-accent text-accent-foreground hover:bg-accent/90">
                  Confirmar Agendamento
                </Button>
              </DialogFooter>
            </form>
          </Form>
        </DialogContent>
      </Dialog>
    </div>
  );
}
